import React from 'react';
import AboutImg from './assets/images/about.svg';
import Slidercommon from './Slidercommon';

const About = () => {
    return (
        <>
        <Slidercommon sldrmainheading="About us"
                      hdescription="Trave Waale is founded in 2020 and it's located in Gurugram (Haryana). 
                                    We provide information about the best places of the world. So you can
                                    find them eaisly."
                      imgsrc={AboutImg}
                      visit='/contact'
                      btnname="Contact Now" />
        </>
    )
}

export default About;